<template>
  <AppRow only-owner>
    <v-col cols="12">
      <DatePicker
        label="Диапазон дат"
        @save="saveDates"
      />
    </v-col>

    <v-col cols="12">
      <v-row>
        <v-col
          v-for="item of cards"
          :key="item.value"
          cols="12"
          sm="6"
          :md="item.value === 'conversionFromRegistrationToDeposit' || item.value === 'conversionToRepeatedDeposit' ? '6' : '4'"
        >
          <InfoCard
            :loading="0"
            :item="item"
            :data="analyticsData"
            property="getAnalyticsData"
            :icon="getIcon(item.value)"
          />
        </v-col>
      </v-row>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Когортный анализ
    </v-col>

    <v-col cols="12">
      <v-card
        :loading="false"
        flat
        class="px-4 pb-2"
      >
        <v-data-table
          :headers="headers"
          :items="[]"
          :items-per-page="5"
          no-data-text="Анализ отсутствует"
        />
      </v-card>
    </v-col>

    <v-col
      cols="12"
      class="text-center text-h5"
    >
      Режимы
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <v-text-field
        v-model.number="bets"
        solo
        flat
        hide-details="auto"
        background-color="#464646"
        label="Кол-во ставок"
        class="rounded-lg"
      />
    </v-col>

    <v-col
      cols="12"
      sm="6"
    >
      <v-text-field
        v-model.number="amount"
        solo
        flat
        hide-details="auto"
        background-color="#464646"
        label="Сумма ставок"
        class="rounded-lg"
      />
    </v-col>

    <v-col>
      <v-card
        :loading="false"
        flat
        class="px-4 pb-2"
      >
        <v-data-table
          :headers="headersGameMode"
          :items="[]"
          :items-per-page="5"
          no-data-text="Анализ отсутствует"
        />
      </v-card>
    </v-col>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import DatePicker from '@/components/DatePicker.vue';
import InfoCard from '@/components/InfoCard.vue';

export default {
  components: {
    AppRow,
    DatePicker,
    InfoCard,
  },
  data() {
    return {
      dateFrom: '',
      dateTo: '',
      cards: [
        {
          text: 'Средний доход с игрока',
          value: 'arpu',
        },
        {
          text: 'Среднее пополнение',
          value: 'replenishment',
        },
        {
          text: 'Среднее кол-во ставок на игрока',
          value: 'bets',
        },
        {
          text: 'Среднее кол-во депозитов на игрока',
          value: 'deposits',
        },
        {
          text: 'DAU (Daily Active Users)',
          value: 'dau',
        },
        {
          text: 'MAU (Monthly Active Users)',
          value: 'mau',
        },
        {
          text: 'Конверсия из регистрации в депозит',
          value: 'conversionFromRegistrationToDeposit',
        },
        {
          text: 'Конверсия в повторный депозит',
          value: 'conversionToRepeatedDeposit',
        },
      ],
      analyticsData: {
        getAnalyticsData: {
          arpu: 3213,
          replenishment: 321,
          bets: 774,
          deposits: 345,
          dau: 12,
          mau: 34,
          conversionFromRegistrationToDeposit: 0.5,
          conversionToRepeatedDeposit: 4,
        },
      },
      headers: [
        { text: 'Дата:', value: 'date', class: 'white--text text-subtitle-2' },
        { text: 'Регистраций:', value: 'registrations', class: 'white--text text-subtitle-2' },
        { text: 'Депозитов:', value: 'deposits', class: 'white--text text-subtitle-2' },
        { text: 'Выводов:', value: 'withdraws', class: 'white--text text-subtitle-2' },
        { text: 'Доход:', value: 'revenue', class: 'white--text text-subtitle-2' },
      ],
      bets: '',
      amount: '',
      headersGameMode: [
        { text: 'Режим', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Кол-во ставок:', value: 'betsQty', class: 'white--text text-subtitle-2' },
        { text: 'Сумма ставок:', value: 'betsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Выиграно:', value: 'winsAmount', class: 'white--text text-subtitle-2' },
        { text: 'Проиграно:', value: 'looseAmount', class: 'white--text text-subtitle-2' },
        { text: 'GGR:', value: 'ggr', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  methods: {
    saveDates(dates) {
      [this.dateFrom, this.dateTo] = dates;
    },
    getIcon(value) {
      if (value === 'arpu' || value === 'replenishment') {
        return 'RUB';
      }

      if (value === 'conversionFromRegistrationToDeposit' || value === 'conversionToRepeatedDeposit') {
        return '%';
      }

      return '';
    },
  },
};
</script>
